////// Styles import //////
function Filters(props) {
  return (
    <>
      <form>
        <input
          className=""
          type="text"
          name=""
          id=""
          value={props.name}
          onChange={props.handleName}
        />
      </form>
    </>
  );
}

export default Filters;
